import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { Button, Input } from "@chakra-ui/react";
import Footer from '../components/Footer';

const EditStudent = () => {
    const { id } = useParams();
    const navigate = useNavigate();
    const [student, setStudent] = useState(null);
    const [fullName, setFullName] = useState("");
    const [address, setAddress] = useState("");
    const [phoneNumber, setPhoneNumber] = useState("");
    const [birthDate, setBirthDate] = useState("");
    const [gender, setGender] = useState("");
    const [programStudy, setProgramStudy] = useState("");
    const [isLoading, setIsLoading] = useState(false); // Set initial state to false

    useEffect(() => {
        const fetchStudentData = async () => {
            setIsLoading(true); // Set isLoading to true before fetching data

            try {
                const response = await fetch(`http://localhost:3001/student/${id}`);
                const data = await response.json();
                setStudent(data);
                setFullName(data.fullname);
                setAddress(data.address);
                setPhoneNumber(data.phoneNumber);
                setBirthDate(data.birthDate);
                setGender(data.gender);
                setProgramStudy(data.programStudy);
            } catch (error) {
                console.log(error);
            } finally {
                setIsLoading(false); // Set isLoading to false after fetching data
            }
        };

        fetchStudentData();
    }, [id]);

    const handleFullNameChange = (e) => {
        setFullName(e.target.value);
    };

    const handleAddressChange = (e) => {
        setAddress(e.target.value);
    };

    const handlePhoneNumberChange = (e) => {
        setPhoneNumber(e.target.value);
    };

    const handleBirthDateChange = (e) => {
        setBirthDate(e.target.value);
    };

    const handleGenderChange = (e) => {
        setGender(e.target.value);
    };

    const handleProgramStudyChange = (e) => {
        setProgramStudy(e.target.value);
    };

    const handleSubmit = async (e) => {
        e.preventDefault();

        const updatedStudent = {
            ...student,
            fullname: fullName,
            address,
            phoneNumber,
            birthDate,
            gender,
            programStudy,
        };

        try {
            const response = await fetch(`http://localhost:3001/student/${id}`, {
                method: "PUT",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify(updatedStudent),
            });
            const data = await response.json();
            console.log(data);
            navigate("/student");
        } catch (error) {
            console.log(error);
        }
    };

    return (
        <div className="edit-student-container">
            {isLoading ? (
                <p>Loading...</p>
            ) : student ? (
                <form className="edit-student-form" onSubmit={handleSubmit}>
                    <div>
                        <label htmlFor="name">Full Name:</label>
                        <Input
                            type="text"
                            id="name"
                            value={fullName}
                            onChange={handleFullNameChange}
                            data-testid="name"
                        />
                    </div>
                    <div>
                        <label htmlFor="profilePicture">Profile Picture:</label>
                        <img
                            src={student.profilePicture}
                            alt="ProfilePicture"
                            data-testid="profilePicture"
                        />
                    </div>
                    <div>
                        <label htmlFor="address">Address:</label>
                        <Input
                            type="text"
                            id="address"
                            value={address}
                            onChange={handleAddressChange}
                            data-testid="address"
                        />
                    </div>
                    <div>
                        <label htmlFor="phoneNumber">Phone Number:</label>
                        <Input
                            type="text"
                            id="phoneNumber"
                            value={phoneNumber}
                            onChange={handlePhoneNumberChange}
                            data-testid="phoneNumber"
                        />
                    </div>
                    <div>
                        <label htmlFor="birthDate">Birth Date:</label>
                        <Input
                            type="text"
                            id="birthDate"
                            value={birthDate}
                            onChange={handleBirthDateChange}
                            data-testid="date"
                        />
                    </div>
                    <div>
                        <label htmlFor="gender">Gender:</label>
                        <Input
                            type="text"
                            id="gender"
                            value={gender}
                            onChange={handleGenderChange}
                            data-testid="gender"
                        />
                    </div>
                    <div>
                        <label htmlFor="programStudy">Program Study:</label>
                        <Input
                            type="text"
                            id="programStudy"
                            value={programStudy}
                            onChange={handleProgramStudyChange}
                            data-testid="programStudy"
                        />
                    </div>
                    <div>
                        <Button type="submit" data-testid="edit-btn">
                            Edit Student
                        </Button>
                    </div>
                </form>
            ) : (
                <p>Student data not found.</p>
            )}
            <Footer />
        </div>

    );
};

export default EditStudent;
